import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Layout from '@/layout'

const routes = [
    {
        path: '/redirect',
        component: Layout,
        hidden: true,
        children: [
            {
                path: '/redirect/:path(.*)',
                component: () => import('@/views/misc/redirect/index')
            }
        ]
    },
    {
        path: '/',
        component: Layout,
        redirect: '/rule'
    },
    {
        path: '/404',
        component: () => import('@/views/misc/error-page/404'),
        hidden: true
    },
    {
        path: '/rule',
        component: Layout,
        redirect: '/rule/ruleList',
        meta: {title: '', icon: 'component'},
        children: [
            {
                path: 'ruleList',
                component: () => import('@/views/rule/RuleList'),
                name: 'RuleList',
                meta: {title: ''}
            }
        ]
    },
    {
        path: '/instance',
        component: Layout,
        redirect: '/instance/instanceList',
        meta: {title: '', icon: 'component'},
        children: [
            {
                path: 'instanceList',
                component: () => import('@/views/instance/InstanceList'),
                name: 'ApplicationList',
                meta: {title: ''}
            }
        ]
    },
    {
        path: '/guide',
        component: Layout,
        redirect: '/guide/guideDetail',
        meta: {title: '', icon: 'component'},
        children: [
            {
                path: 'guideDetail',
                component: () => import('@/views/guide/GuideDetail'),
                name: 'GuideDetail',
                meta: {title: ''}
            }
        ]
    },

    // 404 page must be placed at the end !!!
    {path: '*', redirect: '/404', hidden: true}
]

const router = new VueRouter({
    routes
})

export default router
